	import uniRate from "../../components/uni-rate/uni-rate.vue"
    import {  
        mapState,  
        mapMutations  
    } from 'vuex'; 
	export default {
		data() {
			return {
				loaded: false,
				currentEpd: 1,
				store_id:0,
				storeInfo:{'eval_num':0,'eval_manyi':0},
				evaList:[],
				fav:false,
				maskState: 0, //优惠券面板显示状态
				couponList: [],
				seval_scores:-1
			};
		},
		components: {uniRate},
        computed: mapState(['hasLogin','userInfo']),  
		onLoad(e){
			
			if(e.q){
				let rqurl = decodeURIComponent(e.q);
				this.invitecode = this.$Request.getQueryString(rqurl,'pay_sid');
			}else{
				this.store_id=e.store_id;
			}
			this.loadData();
			console.log('onload');
			console.log(e);
			this.loaded = true;
			
			
		},
		methods:{
			loadData(){
				let _this = this;
				_this.$Request.post('store/store_info',{store_id:_this.store_id}).then(res => {
					if (res.code == 200) {
						console.log(res);
						_this.storeInfo=res.datas.store_info;
						_this.fav = res.datas.is_favorate;
						_this.evaList = res.datas.evaList;
						_this.couponList = res.datas.store_info.vtlist;
						_this.seval_scores = res.datas.store_info.seval_scores;
						 uni.setNavigationBarTitle({
							title: _this.storeInfo.store_name
						}) 
					}
				});
			},
			imageOnLoad(key,index){
				//this.$set(this.data[key][index], 'loaded', 'loaded');
			},
			//收藏
			toFavorite(){
				let _fav = this.fav;
				let url='member_favorites_store/favadd';
				let showinfo_txt = '已收藏';
				if(_fav){url='member_favorites_store/favdel';showinfo_txt = '已取消';}
				this.$Request.post(url,{store_id:this.store_id}).then(res => {
					if (res.code == 200) {
						console.log(res);
						this.fav = !_fav;
						uni.showToast({
							icon: 'none',
							title:showinfo_txt,
							duration:1500
						});
					}else{
						uni.showToast({
							icon: 'none',
							title:res.datas.error,
							duration:2000
						});
					}
				});
			},
			callPhone(phone){
				uni.makePhoneCall({
					phoneNumber: phone 
				});
			},
			toPay:function(){
				 if (!this.hasLogin) {  
					 uni.showToast({
					 	icon: 'none',
					 	title:'请先登录！',
					 	duration:3000,
					 	success: function () {
							 setTimeout(function () {
								uni.navigateTo({
									url:'/pages/member/login',
								});
							  }, 1500);
					 		
					 	}
					 });
				}else{
					uni.navigateTo({
						url:"/pages/store/pay?store_id="+this.store_id
					})
				}
			},
			toLocation: function () {
				uni.openLocation({
					latitude: parseFloat(this.storeInfo.map_lat),
					longitude: parseFloat(this.storeInfo.map_lng),
					success: function () {
						console.log('success');
					}
				});
			},
			toPage: function(url) {
				uni.navigateTo({
					url:url,
				})
			},
			previewImg(src,urls){
				uni.previewImage({
					current:src,
					urls
				})
			},
			stopPrevent(e){
				console.log('======stopPrevent=====');
				console.log(e);
			},
			//显示优惠券面板
			toggleMask(type){
				console.log('======toggleMask====='+type);
				let timer = type === 'show' ? 10 : 300;
				let	state = type === 'show' ? 1 : 0;
				this.maskState = 2;
				setTimeout(()=>{
					this.maskState = state;
				}, timer)
			},
			selectcoupon(e){
				console.log('======selectcoupon=====');
				let _this  = this;
				let selectCouponId=e.voucher_t_id;
				let discount_money=e.voucher_price;
				let selectCouponTxt=e.mtitle;
				this.$Request.post('member_voucher/voucher_freeex',{tid:selectCouponId}).then(res => {
					if (res.code == 200) {
						console.log(res);
						uni.showToast({
							icon: 'none',
							title:'领取成功！',
							duration:3000,
							success: function () {
								 setTimeout(function () {
									_this.toggleMask();
								 }, 2000);
							}
						});
					}
					else{
						uni.showToast({
							icon: 'none',
							title:res.datas.error,
							duration:2000
						});
					}
				}); 
				//this.toggleMask();
			},
			
		},
	}
